.green {
    color: var(--primary);
}

.red {
    color: #ff0000;
}


.featured{
    width: 100%;
    background-color: var(--background);
    padding: 8rem 0;
    clip-path: polygon(0 0, 100% 10%, 100% 100%, 0% 93%);
}

.featured .container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.featured .left{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 2rem;
}

.featured .left p{
    margin: 1.5rem 0;
}

.featured .left .btn{
    align-self: flex-start;
}

.featured .right{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.featured .right .card{
    display: flex;
    flex-direction: column;
    width: 170px;
    height: 170px;
    background-color: var(--background);
    padding: 12px 22px;
    border-radius: 24px;
    margin: .4rem;
    border: 1px solid #d3d3d3;
    box-shadow: rgb(0 0 0 / 12%) 0px 8px 24px;
    text-align: center;
    transition: .3s;
}

.featured .right .card:hover {
    box-shadow: rgb(0 0 0 / 30%) 0px 8px 24px;
}

.featured .card .top img{
    width: 50px;
    margin: auto;
    padding: .2rem 0;
}

.featured .card p{
    margin: .1rem 0;
}

.featured .card span {
    font-size: 21px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.featured .icon {
    font-size: 24px;
    margin-right: 3px;
}



@media screen and (max-width: 940px) {
    .featured .container {
        grid-template-columns: 1fr;
    }

    
}

@media screen and (max-width: 478px) {
    .featured .container .right .card {
        width: 100%;
    }
    .featured .left .btn {
        align-self: center;
    }
}










